const contentful = require("contentful-sync-cli/contentful-local");
const client = contentful.createClient({
	space: "4l0w8syj29ap",
	localPath: "site/cache/contentful"
});

module.exports = client.getEntries({
	content_type: "mediaContact"
}).then(client.unWrap).then(results => {
	return new Promise(resolve => {

		// sort array of media contact objects
		results.items.sort((a,b) => {

			if (a.country < b.country) {
				return -1;
			} else if (a.country > b.country) {
				return 1;
			} else {
				return 0;
			}

		});

		// get the US media contact
		var us = results.items.filter(item => item.country === "United States")[0];

		// fitler the US media contact out of the list
		results.items = results.items.filter(item => item.country !== "United States");

		// put it back at the front
		results.items.unshift(us);

		resolve(results);

	});
});
